import React from 'react'

export const Inicio = ({ setPantalla }) => {

    return (
        <div className="bg-black w-full sm:w-96 m-auto mt-20 p-4 rounded shadow flex flex-col cursor-pointer bg-green-900"
            onClick={ ()=> setPantalla(2) }
        >
                <h3 className="text-white text-2xl text-center uppercase">Iniciar</h3>
            </div>
    )
}
