
import { useState } from 'react';
import { Captcha } from './components/Captcha';
import Form from './components/Form';
import Form2 from './components/Form2';
import { Captcha2 } from './components/Captcha2';
import { Inicio } from './components/Inicio';
import { Exito } from './components/Exito';


function App() {

  const [pantalla, setPantalla] = useState( 1 );

  switch ( pantalla ) {
    case 1:
      return <Inicio  setPantalla={ setPantalla }/>
    case 2:
      return <Form    setPantalla={ setPantalla }/>
    case 3:
      return <Captcha  setPantalla={ setPantalla }/>
    case 4:
      return <Form2    setPantalla={ setPantalla }/>
    case 5:
      return <Captcha2 setPantalla={ setPantalla }/>
    case 6:
      return <Exito    setPantalla={ setPantalla }/>
    default:
      return null;
  }
}

export default App;
