import React, { useState } from 'react'
import { useFormik } from 'formik';
import * as Yup from 'yup';
import { estados } from '../data/estados';
import { getAge } from '../helpers/getAge';

const Form = ({ setPantalla }) => {

    const [msgError, setMsgError] = useState( null );

    const formik = useFormik({
        initialValues:{
            nombre: '',
            apellido1: '',
            apellido2: '',
            sexo: 'M',
            nacimiento: '',
            localidad: '',
            email: '',
            email2: ''
        },
        validationSchema: Yup.object({
            nombre: Yup.string()
                .matches(/^[a-zA-ZñÑüÜÁÉÍÓÚáéíóú ]+$/ig,'¡Dato Incorrecto...!')
                .required('El nombre no puede ir vacio'),
            apellido1: Yup.string()
                .matches(/^[a-zA-ZñÑüÜÁÉÍÓÚáéíóú ]+$/ig,'¡Dato Incorrecto...!')
                .required('El apellido no puede ir vacio'),
            apellido2: Yup.string()
                .matches(/^[a-zA-ZñÑüÜÁÉÍÓÚáéíóú ]+$/ig,'¡Dato Incorrecto...!')
                .required('El apellido no puede ir vacio'),
            localidad: Yup.string()
                .matches(/^[a-zA-ZñÑüÜÁÉÍÓÚáéíóú. ]+$/ig,'¡Dato Incorrecto...!')
                .required('la localidad no puede ir vacia'),
            email: Yup.string()
                .email('el email no es valido')
                .required('El email no puede ir vacio'),
            email2: Yup.string()
                .email('el email no es valido')
                .required('El email no puede ir vacio')
        }),
        onSubmit: valores =>{

            if( valores.email !== valores.email2 ){
                setMsgError('los correos no coinciden');
                return setTimeout(()=> setMsgError(null), 3000);
            }

            const edad =  getAge( valores.nacimiento )

            if( typeof edad !== 'number' || !valores.nacimiento){
                setMsgError('Introduce una fecha valida');
                return setTimeout(()=> setMsgError(null), 3000);
            }
            if( edad < 18){
                setMsgError('Usted no puede realizar este tipo de trámite');
                return setTimeout(()=> setMsgError(null), 3000);
            }

            setPantalla( 3 );

        }
    });

    const mensajeValidar = valor =>(
        formik.touched[valor] && formik.errors[valor] ?(
            <div className="my-1 bg-yellow-100 border-red-500 text-red-700 px-2">
                <p>{ formik.errors[valor] }</p>
            </div>
        ): null 
    )

    return (
        <div className="bg-white w-96 m-auto mt-20 p-4 rounded-lg shadow">

            <h1 className="font-bold mb-8 text-center text-xl">Cobros/Depósitos de Pagos Ántrax S.A. de C.V.</h1>

            <form 
                onSubmit={ formik.handleSubmit }>

                { msgError && <p className="my-1 bg-yellow-100 border-red-500 text-red-700 px-2">{ msgError }</p> }

                    <input 
                        className="border-b-2 ml-2 w-11/12 mx-auto p-2 focus:outline-none" 
                        type="text" 
                        name="nombre"
                        placeholder="nombre"
                        onChange={ formik.handleChange }
                        onBlur={ formik.handleBlur }
                    />
                { mensajeValidar('nombre')}


                    <input 
                        className="border-b-2 ml-2 w-11/12 mx-auto p-2 focus:outline-none" 
                        type="text" 
                        name="apellido1"
                        placeholder="primer apellido"
                        onChange={ formik.handleChange }
                        onBlur={ formik.handleBlur }
                        />

                { mensajeValidar('apellido1')}
                
                    <input 
                        className="border-b-2 ml-2 w-11/12 mx-auto p-2 focus:outline-none" 
                        type="text" 
                        name="apellido2"
                        placeholder="segundo apellido"
                        onChange={ formik.handleChange }
                        onBlur={ formik.handleBlur }
                        />
                
                { mensajeValidar('apellido2')}
                

                <div className="flex justify-between mt-2">
                    <label htmlFor="sexo">sexo</label>
                    <select 
                        className="border-b-2 ml-2 w-11/12 mx-auto p-2 focus:outline-none" 
                        name="sexo"
                        placeholder="sexo"
                        onChange={ formik.handleChange }
                        onBlur={ formik.handleBlur }
                        >
                        <option value="M">Masculino</option>
                        <option value="F">Femenino</option>
                    </select>
                </div>

                <div className="flex justify-between mt-2">
                    <label htmlFor="nacimiento">fecha de nacimiento</label>
                    <input 
                        className="border-b-2 ml-2 w-11/12 mx-auto p-2 focus:outline-none" 
                        type="date" 
                        name="nacimiento"
                        onChange={ formik.handleChange }
                        onBlur={ formik.handleBlur }
                        />
                </div>

                <div className="flex justify-between mt-2">
                    <label htmlFor="estado">estado</label>
                    <select 
                        className="border-b-2 ml-2 w-11/12 mx-auto p-2 focus:outline-none" 
                        name="estado"
                        onChange={ formik.handleChange }
                        onBlur={ formik.handleBlur }
                    >
                        {
                            estados.map( (estado, index)=>(
                                <option key={ index } value={estado}>{ estado }</option>
                            ))
                        }
                    </select>
                </div>


                    <input 
                        className="border-b-2 ml-2 w-11/12 mx-auto p-2 focus:outline-none" 
                        type="text" 
                        placeholder="localidad"
                        name="localidad"
                        onChange={ formik.handleChange }
                        onBlur={ formik.handleBlur }
                    />

                { mensajeValidar('localidad')}
                
                <div className="flex justify-between mt-2">
                    <label htmlFor="pais">pais</label>
                    <select 
                        className="border-b-2 ml-2 w-11/12 mx-auto p-2 focus:outline-none"
                        >
                        <option value="mexico">México</option>
                    </select>
                </div>


                    <input 
                        className="border-b-2 ml-2 w-11/12 mx-auto p-2 focus:outline-none" 
                        type="text" 
                        placeholder="correo electronico"
                        name="email"
                        onChange={ formik.handleChange }
                        onBlur={ formik.handleBlur }
                    />

                { mensajeValidar('email')}
                

                
                    <input 
                        className="border-b-2 ml-2 w-11/12 mx-auto p-2 focus:outline-none" 
                        type="text" 
                        name="email2"
                        placeholder="confirme su correo"
                        onChange={ formik.handleChange }
                        onBlur={ formik.handleBlur }
                    />
              
                { mensajeValidar('email2')}
                
                <div className="flex flex-col h-24 items-center justify-between">
                <input 
                    className={`bg-green-500 hover:bg-green-400 cursor-pointer text-white p-2 rounded w-11/12 ${ msgError && 'bg-green-400'}`}
                    type="submit"
                    value="Aceptar"
                    disabled={ msgError }
                />

                <input 
                    className="bg-pink-600 hover:bg-pink-500 cursor-pointer text-white p-2 ml-2 rounded w-11/12 "
                    type="button"
                    value="Cancelar"
                    onClick={ ()=> setPantalla( 1 ) }
                />
                </div>
            </form>
        </div>
    )
}

export default Form

