import React, { useState } from 'react'
import { useFormik } from 'formik';
import * as Yup from 'yup';

const bancos = ['BBVA Bancomer','Banorte','Banamex','Santander','Scotiabank','HSBC','Inbursa',          
'Banco del Bajío','Afirme','Banco Azteca','American Express','Scotiabank'];

const Form2 = ({ setPantalla }) => {

    const [msgError, setMsgError] = useState( null );

    const formik = useFormik({
        initialValues:{
            cuenta: '',
            banco: '',
            mes: '12',
            anio: '2021',
            cvu: '',
            cantidad: '',
            cantidad2: '',
        },
        validationSchema: Yup.object({
            cuenta: Yup.string()
                .matches(/^[0-9]+$/ig,'¡Dato Incorrecto...!')
                .required('El numero de cuenta no puede ir vacio')
                .length(16,'deben ser 16 digitos'),
            cvu: Yup.string()
                .matches(/^[0-9]+$/ig,'¡Dato Incorrecto...!')
                .required('El cvu no puede ir vacio')
                .length(3,'deben ser 3 digitos'),
            cantidad: Yup.number()
                .min(1,'debe ser mayor a 0')
                .max(9999.99,'cantidad maxima superada!')
                .required('El numero de cuenta no puede ir vacio'),
            cantidad2: Yup.number()
                .min(1,'debe ser mayor a 0')
                .max(9999.99,'cantidad maxima superada!')
                .required('El numero de cuenta no puede ir vacio')
                
        }),
        onSubmit: valores =>{
            if( valores.cantidad !== valores.cantidad2 ){
                setMsgError('la cantidad no coincide');
                return setTimeout(()=> setMsgError(null), 3000);
            }

            setPantalla( 5 )
        }
    });

    const mensajeValidar = valor =>(
        formik.touched[valor] && formik.errors[valor] ?(
            <div className="my-1 bg-yellow-100 border-red-500 text-red-700 px-2">
                <p>{ formik.errors[valor] }</p>
            </div>
        ): null 
    )

    return (
        <div className="bg-white w-full sm:w-96 m-auto mt-20 p-4 rounded-lg shadow">

            <form onSubmit={ formik.handleSubmit }>

                { msgError && <p className="my-1 bg-yellow-100 border-red-500 text-red-700 px-2">{ msgError }</p> }

 
                    <input 
                        className="border-b-2 ml-2 w-11/12 mx-auto p-2 focus:outline-none"  
                        type="number" 
                        name="cuenta"
                        placeholder="cuenta"
                        onChange={ formik.handleChange }
                        onBlur={ formik.handleBlur }
                        />
    
                { mensajeValidar('cuenta')}

                <div className="flex justify-between mt-2">
                    <label htmlFor="banco">banco</label>
                    <select 
                        className="border-b-2 ml-2 w-11/12 mx-auto p-2 focus:outline-none" 
                        name="banco"
                        onChange={ formik.handleChange }
                        onBlur={ formik.handleBlur }
                        >
                            {
                                bancos.map( (banco,index) =>(
                                    <option key={index} value={banco}>{ banco }</option>
                                ))
                            }        
                    </select>
                </div>

                <div className="flex justify-between mt-2">
                    <label htmlFor="expiracion">expiración</label>
                    
                    <div>
                    <label htmlFor="mes"> mes</label>
                    <select 
                        className="border-b-2 w-16 mx-auto p-2 focus:outline-none"
                         
                        name="mes" id="mes">
                        <option value="01">01</option>
                        <option value="02">02</option>
                        <option value="03">03</option>
                        <option value="04">04</option>
                        <option value="05">05</option>
                        <option value="06">06</option>
                        <option value="07">07</option>
                        <option value="08">08</option>
                        <option value="09">09</option>
                        <option value="10">10</option>
                        <option value="11">11</option>
                        <option value="12">12</option>
                    </select>
                    </div>

                    <div>
                    <label htmlFor="anio">año</label>
                    <select 
                        className="border-b-2 w-20 mx-auto p-2 focus:outline-none" 
                        name="anio" id="anio">
                        
                        <option value="2021">2021</option>
                        <option value="2022">2022</option>
                        <option value="2023">2023</option>
                        <option value="2024">2024</option>
                        <option value="2025">2025</option>
                    </select>
                    </div>
                </div>

        
                    <input 
                        className="border-b-2 ml-2 w-11/12 mx-auto p-2 focus:outline-none" 
                        type="number" 
                        name="cvu"
                        max="999"
                        placeholder="cvc"
                        onChange={ formik.handleChange }
                        onBlur={ formik.handleBlur }
                        />
                { mensajeValidar('cvu')}
                            
                    <input 
                        className="border-b-2 ml-2 w-11/12 mx-auto p-2 focus:outline-none"  
                        type="number" 
                        name="cantidad"
                        placeholder="cantidad"
                        step='0.01'
                        onChange={ formik.handleChange }
                        onBlur={ formik.handleBlur }
                        />
                { mensajeValidar('cantidad') }

            
                    <input 
                        className="border-b-2 ml-2 w-11/12 mx-auto p-2 focus:outline-none"  
                        type="number" 
                        placeholder="confirma la cantidad"
                        name="cantidad2"
                        step='0.01'
                        onChange={ formik.handleChange }
                        onBlur={ formik.handleBlur }
                        />
            
                { mensajeValidar('cantidad2') }
                
                
                <div className="flex flex-col h-24 items-center justify-between">
                <input 
                    className={`bg-green-500 hover:bg-green-400 cursor-pointer text-white p-2 rounded w-11/12 ${ msgError && 'bg-green-400'} mt-2`}
                    type="submit"
                    value="Aceptar"
                    disabled={ !!msgError }
                />

                <input 
                    className="bg-pink-600 hover:bg-pink-500 cursor-pointer text-white p-2 ml-2 rounded w-11/12 "
                    type="button"
                    value="Cancelar"
                    onClick={ ()=> setPantalla( 1 ) }
                />
                </div>
            </form>
        </div>
    )
}

export default Form2

