import {useState } from 'react';
import CaptchaLi from 'react-captcha-code';

export const Captcha2 = ({ setPantalla }) => {

    const [palabra, setPalabra] = useState( null );
    const [input, setInput] = useState( '' );
    const [msgError, setMsgError] = useState( null );


    const handleChange = (value) => {
        setPalabra( value )
        console.log( value );
    }

    const handleClick = () => {
        if( input !== palabra ){
            setMsgError('esta mal, vuelve a intentar');
            return setTimeout(()=> setMsgError(null), 3000);
        }

        setPantalla( 6 )
    }
      
    return (
        <div className="bg-white w-full sm:w-96 m-auto mt-20 p-4 rounded shadow">
            

            <div>

                <CaptchaLi 
                    height={ 150}  
                    width={300}
                    charNum={ 6 } 
                    onChange={handleChange} 
                    />
                <input 
                    type="text" 
                    placeholder="escribe aqui"
                    className="border-b-2 ml-2 w-11/12 mx-auto p-2 focus:outline-none mb-4"
                    value={ input }
                    onChange={ ({target})=> setInput( target.value ) }
                />
            </div>
            { msgError && <p className="my-1 bg-yellow-100 border-red-500 text-red-700 px-2">{ msgError }</p> }
            <div className="flex flex-col h-24 items-center justify-between">
            <button 
                className="bg-green-600 hover:bg-green-500 cursor-pointer text-white p-2 ml-2 rounded w-11/12 "
                onClick={ handleClick }
            >Aceptar</button>

            <button 
                className="bg-pink-600 hover:bg-pink-500 cursor-pointer text-white p-2 ml-2 rounded w-11/12 "
                onClick={ ()=> setPantalla( 1 ) }
            >
                Cancelar
            </button>
            </div>
        </div>
    )
}
