import React, { useState, useEffect } from 'react'
import ReCAPTCHA from "react-google-recaptcha";

const clavePublica = '6LdzUBgbAAAAABwk_lzcuqd9NMswwbMNZUIIX_aP';

export const Captcha = ({ setPantalla }) => {
      

    const [counter, setCounter] = useState(0);

    useEffect(() => {
        const intervalID = setInterval(()=> {
            setCounter( counter + 1 )
            console.log('interval');
        }, 1000);

        return () => {
            window.clearInterval( intervalID );
        }

    }, [counter])

    if( counter > 60 ){
        setPantalla( 1 );
    }

    return (
        <div className="bg-white w-full sm:w-96 m-auto mt-20 p-4 rounded shadow flex flex-col items-center">

            <ReCAPTCHA
                sitekey={ clavePublica }
                onChange={ ()=> setPantalla( 4 ) }
            />

            tiempo:
            <div className="text-7xl text-pink-600">{ counter }</div>

            <button 
                className="bg-pink-600 hover:bg-pink-500 cursor-pointer text-white p-2 ml-2 rounded w-11/12 mt-4 "
                onClick={ ()=> setPantalla( 1 ) }
            >
            Cancelar
            </button>
        </div>
    )
}
