import React from 'react'

export const Exito = ({ setPantalla }) => {

    setTimeout(()=> setPantalla( 1 ),3000);
    
    return (
        <>
            <div className="bg-black w-full sm:w-96 m-auto mt-20 p-4 rounded shadow flex flex-col">
                <h3 className="text-white text-2xl text-center">Envió realizado correctamente</h3>
            </div>
        </>
    )
}
